import "./style.css";
import {Swiper, SwiperSlide} from "swiper/react";
import {useState, useEffect} from "react";
import {apiRoot} from "../../constants";
import http from "../../services/httpService.js";

function MainSlider() {
  const [sliders, setSliders] = useState([]);
  useEffect(() => {
    const getUrl = async () => {
      let res = await http.get(apiRoot + "/site-data/sliders/");
      setSliders(res.data.data);
    };
    getUrl();
  }, []);
  return (
    <>
      <section
        id="aa-slider"
        className="mb-4"
        style={{marginBottom: "100px"}}
      >
        <div className="aa-slider-area">
          <div className="seq-screen">
            <Swiper
              navigation
              spaceBetween={0}
              slidesPerView={1}
              loop={{loop: true}}
              pagination={{clickable: true}}
              autoplay={{delay: 5000, disableOnInteraction: false}}
            >
              {[...sliders.entries()].map(([index, slider]) => (
                <SwiperSlide key={index}>
                  <div className="seq-model">
                    <img
                      src={slider.image}
                      className="rounded d-block img-fluid"
                      alt="#"
                    />
                  </div>

                  <div className="seq-title">
                    <span>Save Up to 75% Off</span>
                    <h2>{slider.title}</h2>
                    <p>{slider.text}</p>

                    <a
                      href="/store"
                      className="btn  myBtn  text-white md-round py-3 px-4"
                      tabindex="0"
                      style={{backgroundColor: "#5ba515"}}
                    >
                      Shop Now <i className="fas fa-arrow-right ml-2"></i>
                    </a>
                  </div>
                </SwiperSlide>
              ))}
            </Swiper>
          </div>
        </div>
      </section>


    </>
  );
}

export default MainSlider;
