import "./css.css";
import React, {useEffect, useState} from "react";
import {AllHeaderBackground} from "../helpers";
import {getCurrentUser} from "../services/authService";
import {apiRoot, domainRoot} from "../constants";
import {Link} from "react-router-dom";
import http from "../services/httpService";

function Profile() {
  const [onWay, setOnWay] = useState([]);
  const [delivered, setDelivered] = useState([]);

  useEffect(() => {
    const getUrl = async () => {
      let res = await http.get(apiRoot + "/purchase/way_list/");
      setOnWay(res.data);
      res = await http.get(apiRoot + "/purchase/delivered_list/");
      setDelivered(res.data);
    }
    getUrl();
  }, []);

  const user = getCurrentUser();
  return (
    <>
      <AllHeaderBackground label={"Profile"}/>
      <div className="container mt-4 mb-4">
        <div className="main-body">
          <div className="row gutters-sm">
            <div className="col-md-4 mb-3">
              <div className="card">
                <div className="card-body">
                  <div className="d-flex flex-column align-items-center text-center">
                    <img
                      src={user.profile || "https://bootdey.com/img/Content/avatar/avatar7.png"}
                      alt="Admin"
                      className="rounded-circle"
                      width="150"
                    />
                    <a className="btn btn-success"
                       href={domainRoot + '/users/update-profile-image/?email=' + user.email}>Change profile picture</a>
                  </div>
                </div>
              </div>
              <div className="card mt-3">
                <ul className="list-group list-group-flush">
                  <li className="list-group-item d-flex justify-content-between align-items-center flex-wrap">
                    <a className="mb-0 h6" href={user.facebook}>
                      <span className="icon-facebook"/> Facebook
                    </a>
                  </li>
                  <li className="list-group-item d-flex justify-content-between align-items-center flex-wrap">
                    <a className="mb-0 h6" href={user.twitter}>
                      <span className="icon-twitter"/> Twitter
                    </a>
                    <span className="text-secondary"/>
                  </li>
                </ul>
              </div>
            </div>
            <div className="col-md-8">
              <div className="card mb-3">
                <div className="card-body">
                  <div className="row">
                    <div className="col-sm-3">
                      <h6 className="mb-0">Full Name</h6>
                    </div>
                    <div className="col-sm-9 text-secondary">{user.name || "Not Provided"}</div>
                  </div>
                  <hr/>
                  <div className="row">
                    <div className="col-sm-3">
                      <h6 className="mb-0">Email</h6>
                    </div>
                    <div className="col-sm-9 text-secondary">{user.email}</div>
                  </div>
                  <hr/>
                  <div className="row">
                    <div className="col-sm-3">
                      <h6 className="mb-0">Phone</h6>
                    </div>
                    <div className="col-sm-9 text-secondary">{user.phone || "Not Provided"}</div>
                  </div>
                  <hr/>
                  <div className="row">
                    <div className="col-sm-3">
                      <h6 className="mb-0">Address</h6>
                    </div>
                    <div className="col-sm-9 text-secondary">
                      {user.address || "Not Provided"}
                    </div>
                  </div>
                </div>
              </div>
              <a className="btn-lg btn-success h1" href={domainRoot + '/users/update-info/?email=' + user.email}>Edit
                information</a>
            </div>
          </div>
        </div>
      </div>
<div className="container">
  

      < h2 className="col-lg-6 ">On way</h2>
      <div className="col-lg-12">
        <div className="table-main table-responsive">
          <table className="table">
            <thead>
            <tr>
              <th>Images</th>
              <th>Product Name</th>
              <th>Price</th>
              <th>Quantity</th>
              <th>Total</th>
            </tr>
            </thead>
            <tbody>
            {onWay.map((cart) => (
              <tr>
                <td className="thumbnail-img">
                  <img
                    className="img-fluid thumbnail-img"
                    src={cart.product.images[0]}
                    alt="Product"
                  />
                </td>
                <td className="name-pr">
                  <Link to={`/item/${cart.product.id}`}>
                    {cart.product.name}
                  </Link>
                </td>
                <td className="price-pr">
                  <p>${cart.product.price}</p>
                </td>
                <td className="quantity-box">

                  <div className="quantity-box2">{cart.quantity}</div>

                </td>
                <td className="total-pr">
                  <p>${cart.product.price * cart.quantity}</p>
                </td>
              </tr>
            ))}
            </tbody>
          </table>
        </div>
      </div>
      <h2 className="col-lg-6">Delivered</h2>
      <div className="col-lg-12">
        <div className="table-main table-responsive">
          <table className="table">
            <thead>
            <tr>
              <th>Images</th>
              <th>Product Name</th>
              <th>Price</th>
              <th>Quantity</th>
              <th>Total</th>
            </tr>
            </thead>
            <tbody>
            {delivered.map((cart) => (
              <tr>
                <td className="thumbnail-img">
                  <img
                    className="img-fluid thumbnail-img"
                    src={cart.product.images[0]}
                    alt="Product"
                  />
                </td>
                <td className="name-pr">
                  <Link to={`/item/${cart.product.id}`}>
                    {cart.product.name}
                  </Link>
                </td>
                <td className="price-pr">
                  <p>${cart.product.price}</p>
                </td>
                <td className="quantity-box">

                  <div className="quantity-box2">{cart.quantity}</div>

                </td>
                <td className="total-pr">
                  <p>${cart.product.price * cart.quantity}</p>
                </td>
              </tr>
            ))}
            </tbody>
          </table>
        </div>
      </div>
      </div>
    </>
  );
}

export default Profile;
