import React, {useEffect, useState} from "react";
import {apiRoot} from "../constants";
import http from "../services/httpService.js";
import CategoryItems from "./cateoryItems.jsx";
import {AllHeaderBackground} from "../helpers";
import {Link} from "react-router-dom";

function Store({match, history}) {
  const {catName} = match.params;
  const [categories, setCategories] = useState([]);
  const [category, setCategory] = useState([]);
  const [, setTax] = useState(0);
  const [search, setSearch] = useState('');

  const handleChange = ({currentTarget: input}) => {
    if(input.name === 'search') setSearch(input.value);
  }

  useEffect(() => {
    const getUrl = async () => {
      let res = await http.get(apiRoot + "/site-data/tax_rate/");
      setTax(res.data.data);
      res = await http.get(apiRoot + "/category/");
      setCategories(res.data);
    };
    getUrl();
  }, []);

  useEffect(() => {
    const getUrl = async () => {
      let res = await http.get(apiRoot + "/category/");
      setCategory(res.data);
    };
    getUrl();
  }, []);
  return (
    <main>
      <section className="introBannerHolder d-flex w-100 bgCover all-title-box">
        <div className="container">

          <AllHeaderBackground lable={"Shop"}/>
        </div>
      </section>

      <div className="twoColumns container pt-lg-23 pb-lg-20 pt-md-16 pb-md-4 pt-10 pb-4">
        <div className="row">
          <div className="col-12 col-lg-9 order-lg-3">
            <article id="content">
              <header className="show-head d-flex flex-wrap justify-content-between mb-7">
                <ul className="list-unstyled viewFilterLinks d-flex flex-nowrap align-items-center">
                  <li className="mr-2">
                    <p className="active">
                      <i className="fas fa-th-large"/>
                    </p>
                  </li>
                  <li className="mr-2">
                    <p>
                      <i className="fas fa-list"/>
                    </p>
                  </li>
                  <li className="mr-2">Showing 19 of 24 results</li>
                </ul>

                <div className="sortGroup">
                  <div className="d-flex flex-nowrap align-items-center">
                    <strong className="groupTitle mr-2">Sort by:</strong>
                    <div className="dropdown">
                      <button
                        className="dropdown-toggle buttonReset"
                        type="button"
                        id="sortGroup"
                        data-toggle="dropdown"
                        aria-haspopup="true"
                        aria-expanded="false"
                      >
                        Default sorting
                      </button>
                      <ul
                        className="dropdown-menu dropdown-menu-right"
                        aria-labelledby="sortGroup"
                      >
                        <li>
                          <p>Default Order</p>
                        </li>
                        <li>
                          <p>Default Order</p>
                        </li>
                        <li>
                          <p>Default Order</p>
                        </li>
                        <li>
                          <p>Default Order</p>
                        </li>
                      </ul>
                    </div>
                  </div>
                </div>
              </header>
              <div className="row">
                {!catName &&
                category.map((cat) => (
                  <CategoryItems key={cat.id} catId={cat.id} cat={cat.name}/>
                ))}
                {catName && <CategoryItems catId={5} cat={catName}/>}
              </div>
            </article>
          </div>
          <div className="col-12 col-lg-3 order-lg-1">
            <aside id="sidebar">
              <section className="widget overflow-hidden mb-9">
                <form
                  className="searchForm position-relative border"
                >
                  <fieldset>
                    <input
                      type="search"
                      className="form-control"
                      placeholder="Search product..."
                      onChange={handleChange}
                      value={search}
                      name='search'
                      onSubmit={() => history.push(`/search/${search}/`)}
                    />
                    <button className="position-absolute" onClick={() => history.push(`/search/${search}/`)}>
                      <i className="icon-search"/>
                    </button>
                  </fieldset>
                </form>
              </section>

              <section className="widget overflow-hidden mb-9">
                <h3 className="headingVII fwEbold text-uppercase mb-5">
                  PRODUCT CATEGORIES
                </h3>
                <ul className="list-unstyled categoryList mb-0">
                  <li className="mb-5 overflow-hidden"><Link to="/store">All</Link></li>
                  {categories.map((category) => (
                    <li key={category.id} className="mb-5 overflow-hidden">
                      <a href={"/store/" + category.name}>
                        {category.name}
                        <span className="num border float-right">
                          {category.count}
                        </span>
                      </a>
                    </li>
                  ))}
                </ul>
              </section>

              <section className="widget mb-9">
                <h3 className="headingVII fwEbold text-uppercase mb-6">
                  Filter by price
                </h3>

                <form className="filter-ranger-form">
                  <div id="slider-range"/>
                  <input type="hidden" id="amount1" name="amount1"/>
                  <input type="hidden" id="amount2" name="amount2"/>
                  <div className="get-results-wrap d-flex align-items-center justify-content-between">
                    <button
                      type="button"
                      className="btn btnTheme btn-shop fwEbold md-round px-3 pt-1 pb-2 text-uppercase"
                    >
                      Filter
                    </button>
                    <p id="amount" className="mb-0"/>
                  </div>
                </form>
              </section>
            </aside>
          </div>
        </div>
      </div>
    </main>
  );
}

export default Store;
