import React, {useEffect, useState} from "react";
import http from "../../services/httpService.js";
import {apiRoot} from "../../constants";
import border from "../../images/hbdr.png"
import SwiperCore, {Navigation, Autoplay} from "swiper";
import {Swiper, SwiperSlide} from "swiper/react";
import Item from "../../item/item.jsx";

SwiperCore.use([Navigation, Autoplay]);

function Recommended() {
  const [recommends, setRecommends] = useState([]);

  useEffect(() => {
    const getUrl = async () => {
      let res = await http.get(apiRoot + "/recommend/");
      setRecommends(res.data);
    };
    getUrl();
  }, []);
  return (
    <section className="featureSec container overflow-hidden pt-xl-12 pb-xl-9 pt-lg-10 pb-lg-4 pt-md-8 pb-md-2 pt-5">
      <div className="row">
        <header className="col-12 mainHeader mb-4 text-center">
          <h1 className="headingIV playfair fwEblod mb-4">New Arrival</h1>
          <span className="headerBorder d-block mb-5">
						<img
              src={border}
              alt="Header Border"
              className="img-fluid img-bdr"
            />
					</span>
          <p>There are many variations of passages of lorem ipsum available </p>
        </header>
      </div>
      <Swiper
        spaceBetween={50}
        slidesPerView={1}
        navigation
        breakpoints={{
          360: {
            slidesPerView: 1,
          },
          500: {
            slidesPerView: 1,
          },
          640: {
            slidesPerView: 3,
          },
          1100: {
            slidesPerView: 4,
          },
        }}
        autoplay={{delay: 3000, disableOnInteraction: false}}
        pagination={{clickable: true}}
      >
        {[...recommends.entries()].map(([index, recommend]) => (
          <SwiperSlide key={index}>
            <Item item={recommend}/>
          </SwiperSlide>
        ))}
      </Swiper>
    </section>
  );
}

export default Recommended;
