import "./style.css";
import React, {useEffect, useState} from "react";
import {apiRoot} from "../constants";
import http from "../services/httpService.js";
import {logout} from "../services/authService";
import {Link, NavLink} from "react-router-dom";
import CartMenu from "./navbarCartMenu.jsx";
import BootstrapNavbar from "./bootstrapNavbar";
import {BropdownToggleButton, Loding, LoginLogout, NavbarButton, NavbarLogo,} from "../helpers";

function TopMainNavbar({history}) {
  const [categories, setCategories] = useState([]);
  const [carts, setCarts] = useState([]);
  const [tax, setTax] = useState(0);
  const [isLoading, setLoading] = useState(true);

  useEffect(() => {
    const unListen = history.listen(() => {
      window.scrollTo(0, 0);
    });
    return () => {
      unListen();
    };
    // eslint-disable-next-line
  }, []);

  useEffect(() => {
    const getUrl = async () => {
      try {
        let res = await http.get(apiRoot + "/purchase/cart_list/");
        setCarts(res.data);
        setLoading(false);
      } catch (e) {
        setLoading(false);
      }

    };
    getUrl();
  }, [carts]);

  useEffect(() => {
    const getUrl = async () => {
      let res = await http.get(apiRoot + "/site-data/tax_rate/");
      setTax(res.data.data);
      res = await http.get(apiRoot + "/category/");
      setCategories(res.data);

    };
    getUrl();
  }, []);
  const handleScroll = () => {
    let navbar = document.getElementById("navbar-1");
    let navbarx = document.getElementById("navbar-2");
    let sticky = navbar.offsetTop;
    if (window.pageYOffset >= sticky) {
      navbarx.classList.add("d-flex");
      navbarx.classList.remove("d-none");
    } else {
      navbarx.classList.remove("d-flex");
      navbarx.classList.add("d-none");
    }
  };
  useEffect(() => {
    if (window && !isLoading) {
      window.addEventListener("scroll", handleScroll);
    }
    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, [isLoading]);
  return (
    <>
      {isLoading && <Loding/>}
      {!isLoading && (
        <>
          <header id="header" className="position-relative">
            <div className="headerHolder container pt-lg-5 pb-lg-7 py-4">
              <div className="row">
                <div className="col-6 col-sm-2">
                  <NavbarLogo/>
                </div>
                <div className="col-6 col-sm-7 col-lg-8 static-block">
                  <div className="mainHolder pt-lg-5 pt-3 justify-content-center">
                    <nav className="navbar navbar-expand-lg navbar-light p-0 pageNav2 position-static">
                      <NavbarButton idName={"navbarNav"}/>
                      <div className="collapse navbar-collapse" id="navbarNav">
                        <ul className="navbar-nav mx-auto text-uppercase d-inline-block">
                          <li className="nav-item dropdown">
                            <NavLink className="d-block" to="/">
                              Home
                            </NavLink>
                          </li>
                          <li className="nav-item dropdown">
                            <BropdownToggleButton label={"Store"}/>
                            <ul className="list-unstyled text-capitalize dropdown-menu mt-0 py-0">
                              {categories.map((category) => (
                                <li
                                  key={category.id}
                                  className="d-block mx-0 nav-item"
                                >
                                  <Link to={"/store/" + category.name}>
                                    {category.name}
                                  </Link>
                                </li>
                              ))}
                            </ul>
                          </li>
                          <li className="nav-item">
                            <Link className="d-block" to="/about">
                              About
                            </Link>
                          </li>
                          <li className="nav-item dropdown">
                            <BropdownToggleButton label={"Pages"}/>
                            <ul className="list-unstyled text-capitalize dropdown-menu mt-0 py-0">
                              <li className="d-block mx-0">
                                <NavLink to="/cart">Cart Page</NavLink>
                              </li>
                              <li className="d-block mx-0">
                                <NavLink to="/checkout">Check Out</NavLink>
                              </li>
                            </ul>
                          </li>
                          <li className="nav-item">
                            <NavLink className="d-block" to="/contactus">
                              contact
                            </NavLink>
                          </li>
                          <li className="nav-item dropdown">
                            <BropdownToggleButton label={"Acccount"}/>
                            <ul className="list-unstyled text-capitalize dropdown-menu mt-0 py-0">
                              <LoginLogout/>
                            </ul>
                          </li>
                        </ul>
                      </div>
                    </nav>
                  </div>
                </div>
                <div className="col-sm-3 col-lg-2">
                  <CartMenu categorie={categories} carts={carts} tax={tax}/>
                </div>
              </div>
            </div>
          </header>

          <div id="navbar-1"/>
          <div id="navbar-2" className="d-none fixed-top ">
            <BootstrapNavbar
              categories={categories}
              logout={logout}
              history={history}
            />
          </div>
        </>
      )}
    </>
  );
}

export default TopMainNavbar;
