import {useState} from "react";
import {Link} from "react-router-dom";
import {BropdownToggleButton, LoginLogout, NavbarButton, NavbarLogo,} from "../helpers";

function BootstrapNavbar({categories, history}) {
  const handleChange = ({currentTarget: input}) => {
    setSearch(input.value);
  };
  const [search, setSearch] = useState("");
  return (
    <div>
      <nav className="navbar navbar-expand-lg navbar-dark ">
        <NavbarLogo/>
        <NavbarButton idName={"navbarSupportedContent"}/>

        <div className="collapse navbar-collapse " id="navbarSupportedContent">
          <ul className="navbar-nav">
            <li className="nav-item active">
              <Link className="nav-link" to="/">
                Home <span className="sr-only ">(current)</span>
              </Link>
            </li>

            <li className="nav-item dropdown">
              <BropdownToggleButton url="/store/" label={"Store"}/>
              <div className="dropdown-menu" aria-labelledby="navbarDropdown">
                {categories.map((category) => (
                  <Link
                    to={"/store/" + category.name}
                    className="dropdown-item"
                    key={category.id}
                  >
                    {category.name}
                  </Link>
                ))}
              </div>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/cart">
                Cart <span className="icon-cart "/>
              </Link>
            </li>
          </ul>
        </div>


        <ul className="navbar-nav ml-auto pull-right">
          <li className="nav-item">
            <div className="header-advance-search">
              <form>
                <input
                  placeholder="Search your product"
                  onSubmit={() => history.push(`/search/${search}`)}
                  onChange={handleChange}
                />
                <button onClick={() => history.push(`/search/${search}`)}>
                  <span className="icon-search " style={{color: "white"}}/>
                </button>
              </form>
            </div>
          </li>
        </ul>

        <LoginLogout/>

      </nav>

    </div>
  );
}

export default BootstrapNavbar;
