import "./App.css";
import "./style.css";
import "./css/fontawesome.css";
import "./css/responsive.css";
import "./css/bootstrap.css";
import "./css/plugins.css";
import "./css/responsive-custome.css";
import Navbar from "./navbar/topMainNavbar.jsx";
import HomePage from "./homepage/homePage.jsx";
import FooterHolder from "./footer/foooterHolder.jsx";
import Footer from "./footer/footer.jsx";
import FAQ from "./FAQ/FAQ.jsx";
import Cart from "./cart/cart.jsx";
import ShopItem from "./shop/shop";
import Order from "./ordertakepage/order";
import Search from "./search/search.jsx";
import Login from "./login/login.jsx";
import Register from "./login/register";
import { Route, Switch } from "react-router-dom";
import Tech from "./tech/tech.jsx";
import Store from "./store/store.jsx";
import "./icomoon/style.css";
import { ToastContainer } from "react-toastify";
import "bootstrap";
import "bootstrap/dist/css/bootstrap.css";
import "bootstrap/dist/js/bootstrap.js";
import Account from "./profile/profile.jsx"
function App() {
	return (
		<div id="pageWrapper">
			<ToastContainer />
			
			<Route path="/" component={Navbar} />
			<main>
				<Switch>
					<Route path="/faq" component={FAQ} />
					<Route path="/contactus" component={Tech} />
					<Route path="/cart" component={Cart} />
					<Route
						path="/item/:itemId"
						component={ShopItem}
						render={() => <ShopItem />}
					/>
					<Route path="/login" component={Login} />
					<Route
						path="/search/:itemName"
						component={Search}
						render={() => <Search />}
					/>
					<Route path="/register" component={Register} />
					<Route path="/checkout" component={Order} />
					<Route path="/store/:catName" component={Store} />
					<Route path="/store" component={Store} />
					<Route path="/account" component={Account} />
					<Route path="/" component={HomePage} />
				</Switch>
				<FooterHolder />
			</main>
			<Footer />
		</div>
	);
}

export default App;
